import { createApp } from 'vue';
import App from './App.vue';
import { ElMessage } from 'element-plus';
import ElementPlus from 'element-plus';
import 'element-plus/dist/index.css';
import router from './router';
import store from './store';
import "animate.css";
// import 'element-plus/lib/theme-chalk/index.css'

import fetch from './fetch/fetch';
import md5 from 'md5';
import token from './token';
import * as echarts from 'echarts';
  
const app = createApp(App);

// md5加密秘钥
const secret = '李勇良';

// 设置cookie
function setCookie(c_name, value, expiredays) {
    if (expiredays){
      var exdate = new Date();
      exdate.setDate(exdate.getDate() + expiredays);
      document.cookie = c_name + "=" + escape(value) + ";expires = " + exdate.toGMTString() + ";path = /;";
    }else{
      document.cookie = c_name + "=" + escape(value) + ";path = /;";
    }
    
  }
  // 获取cookie
  function getCookie(name) {
    var arr;
    var reg = new RegExp("(^| )" + name + "=([^;]*)(;|$)");
    arr = document.cookie.match(reg);
    if(document.cookie.match(reg)){
        return unescape(arr[2]);
    } else{
        return '';
    }
  }

  // 删除cookie
function delCookie(name, domain, value = '') {
    var exdate = new Date();
    exdate.setDate(exdate.getDate() - 1);
    if (name != null && domain) {
      document.cookie = name + "=" + escape(value) + ";expires = " + exdate.toGMTString() + ";path = /;domain=" + domain;
    } else {
      document.cookie = name + "=" + escape(value) + ";expires = " + exdate.toGMTString() + ";path = /;";
    }
  }

// vue3 给原型上挂载属性
app.config.globalProperties.$fetch = fetch;
app.config.globalProperties.$echarts = echarts;
app.config.globalProperties.$token = token;
app.config.globalProperties.$setCookie = setCookie;
app.config.globalProperties.$getCookie = getCookie;
app.config.globalProperties.$delCookie = delCookie;

// 密码加密
app.config.globalProperties.$md5 = function(password){
    return md5(toString(secret+ password +secret))
};

app.use(ElementPlus);
app.use(store).use(router).mount('#app');


// 路由守卫
router.beforeEach((to, from, next) => {
    if(to.meta.title){
        document.title=to.meta.title
    }

    const verifyToken = token.verify(localStorage.getItem('token'));
    
    // 路由校验token是否过期
    if(to.path == '/login'){
        next();
    } else if(verifyToken && verifyToken.success){
        // 没有admin权限访问，报错提示返回 user 页面 判断条件：token没有admin 状态，跳转路由为admin
        if(!verifyToken.decoded.data.admin && to.meta.admin){
            ElMessage({type:"error",message:'您无权访问该页面'});
            next('/user');
        } else{
            next();
        }
       
    // 管理员权限
    }  else{
        console.log('跳转登录页面');
        // token 过期清理 token
        localStorage.removeItem("token");
        ElMessage({type:"error",message:'token过期请重新登录'});
        next('/login')
    }
    
    // next();
})


export default app;
